/**  Basic Page CSS **/
body {
  font-weight: 200;
 
}

header a,
footer a {
  color: white;
}
.App-header {
  padding-left: 0.5em;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  font-size: calc(10px + 2vmin);
  background-image: -webkit-radial-gradient(center ,circle farthest-side ,#009cde,#003087 125%);
  background-image: radial-gradient(circle farthest-side at center ,#009cde,#003087 125%);
}
footer {
  font-size: 0.8em;
}

.bg-primary,
.btn-primary {
  background-color: #377bb5 !important;
  border: inherit;
}
.add-appointment .card-body {
  display: none;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}


.App-footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  /* Set the fixed height of the footer here */
  height: 50px;
  line-height: 40px; /* Vertically center the text there */
  background-color: #f5f5f5;
  color:white;
  background-image: -webkit-radial-gradient(center ,circle farthest-side ,#009cde,#003087 125%);
  background-image: radial-gradient(circle farthest-side at center ,#009cde,#003087 125%);
}

.App-link {
  color: #61dafb;
}
.apt-addheading{
  color: #61dafb;
  background-image: -webkit-radial-gradient(center  ,circle farthest-side  ,#009cde,#003087 125%);
  background-image: radial-gradient(circle farthest-side at center  ,#009cde,#003087 125%);
}
.dropdown-item.active,
.dropdown-item:active {
  background-image: -webkit-radial-gradient(center ,circle farthest-side ,#009cde,#003087 125%);
  background-image: radial-gradient(circle farthest-side at center ,#009cde,#003087 125%);
}

.bg-primary, .btn-primary{
  color: #61dafb;
  background-image: -webkit-radial-gradient(center ,circle farthest-side ,#009cde,#003087 125%);
  background-image: radial-gradient(circle farthest-side at center ,#009cde,#003087 125%);
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

